import React from "react";
import Chart from "react-apexcharts";

import Container from "react-bootstrap/Container";

function ScatterChartBetaLessThanOne(props) {

    let areaoptions = {
        chart: {
            type: 'area',
            toolbar: {
                show: false,
            }
        },
        dataLabels: {
            enabled: false
        },
        stroke: {
            curve: 'smooth'
        },
        xaxis: {
            type: 'datetime',
            categories: [...new Array(50)].map((item, index) => { var d = new Date(); return d.setMonth(d.getMonth() - 50 + index) })
        },
        tooltip: {
            x: {
                format: 'dd/MM/yy'
            },
        },
    };

    let scatteroptions = {
        chart: {
            // height: 350,
            type: 'scatter',
            toolbar: {
                show: false,
            },
            zoom: {
                enabled: true,
                type: 'xy'
            }
        },
        xaxis: {
            title: {
                text: 'Доходность фондового индекса за месяц',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-yaxis-title',
                }
            },
            tickAmount: 11,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            }
        },
        yaxis: {
            title: {
                text: 'Доходность актива за месяц',
                style: {
                    color: "gray",
                    fontSize: '1rem',
                    fontFamily: 'Helvetica, Arial, sans-serif',
                    fontWeight: 200,
                    cssClass: 'apexcharts-yaxis-title',
                }
            },
            tickAmount: 11,
            labels: {
                formatter: function (val) {
                    return parseFloat(val).toFixed(1)
                }
            }
        }
    };


    let scatterseries = [
        {
            name: "Доходность актива за период",
            data: [...new Array(49)].map(() => { let randomReturn = Math.round(Math.random() * 300 - 140) / 10; return [randomReturn, randomReturn * (1 - Math.random())] })
        }
    ];

    let indexData = [100];
    let assetData = [100];
    scatterseries[0].data.map((item, index) => {
        indexData[index + 1] = Math.round(indexData[index] * (1 + item[0] / 100));
        assetData[index + 1] = Math.round(assetData[index] * (1 + item[1] / 100));
        return null
    })

    // console.log(indexData);
    // console.log(assetData);

    let areaseries = [{
        name: 'Индекс',
        data: indexData
    }, {
        name: 'Актив',
        data: assetData
    }];

    return <Container className="mt-3" >
        <Chart options={scatteroptions} series={scatterseries} type="scatter" />
        <Chart options={areaoptions} series={areaseries} type="area" />
    </Container>

}

export default ScatterChartBetaLessThanOne;
